@use '../../vars' as *;
@use '../../abstract' as *;
@use '../../../../node_modules/sass-mq/mq';

.dynamic-list {
    border: 0;
    padding-bottom: var(--footer-height);
    box-sizing: border-box;

    &__header {
        position: sticky;
        top: var(--header-height);
        background: $black;
        padding: rem(20);
        z-index: 10;
    }

    &__counter {
        color: $white;
        padding: rem(40) rem(40) 0;
    }

    &__container {
        padding: rem(20);
        width: 100%;
        box-sizing: border-box;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: rem(50);
    }

    &__label {
        color: $white;
        padding: rem(20);
    }

    &__top {
        gap: rem(10);
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }
}
